<template>
  <div class="content">
    <section class="top">
      <van-dropdown-menu :overlay="false">
        <van-dropdown-item
          v-for="(item,index) in navBarJson"
          :key="index"
          :title="item.title"
          :ref="item.json"
        >
          <div class="btnContent">
            <div v-for="(e,index) in item.itemJson" :key="index" @click="clectBtn(e.id)">
              <div :class="['btn',e.active?'active':'true']">{{e.name}}</div>
            </div>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </section>
    <section class="item">
      <div class="itemContent" v-for="(item,index) of this.getRespone.openCardItem" :key="index">
        <div class="titlte">
          <div class="headIco" :style="{'background':item.sex != 1 ? '#F4B3B3':'#A0D9F6'}"></div>
          <p>{{item.num}}</p>
        </div>
        <div class="middleContent">
          <div class="line">
            <p>手牌类型</p>
            <p>{{item.type == 1 ? '单人手牌':'团队手牌'}}</p>
          </div>
          <div class="line" v-show="item.type != 1">
            <p>团队手牌</p>
            <p>{{item.teaNum}}</p>
          </div>
        </div>
        <div class="bottom">
          <div class="left">
            <div class="icoDiv" style="background:#6CAFF5" v-if="item.type == 1">
              <van-icon name="manager-o" color="#FFF" size="20"/>
            </div>
            <div class="icoDiv" style="background:#F2795B" v-else>
              <van-icon name="friends-o" color="#FFF" size="20"/>
            </div>
            <p>{{item.type != 1 ? '团队手牌':'单人手牌'}}</p>
          </div>
          <div class="right">
            <p>{{item.openTime}}</p>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navBarJson: this.getMeanJson.headMean,
      switch1: false,
      switch2: false,
      item: "json"
    };
  },
  created() {
    // 调用 vant 组件的toggle；默认打开导航栏选择
    setTimeout(() => {
      this.$refs.items[0].toggle();
    }, 0.1);
  },
  methods: {
    clectBtn(id) {
      var json = this.navBarJson;
      json.forEach(el => {
        el.itemJson.forEach(element => {
          if (id == element.id) {
            element.active = !element.active;
          }else{
            element.active = false;
          }
        });
      });
      this.navBarJson = json;
    }
  }
};
</script>

<style lang="scss" scoped>
.btnContent {
  @include fw(flex-start);
  @include wh(100%, auto);
  padding: 0.8rem;
  .btn {
    @include sc(0.8rem, $grayfont);
    @include wh(4rem, 2rem);
    padding: 0.5rem;
    background-color: $Tgray;
    border-radius: 1rem;
    margin-right: 0.5rem;
    text-align: center;
  }
  .active {
    @include sc(0.8rem, $fc);
    background-color: $blue;
  }
}
.item {
  margin-top: 3.5rem;
  padding: 0.5rem 0.8rem;
  overflow: scroll;
  position: absolute;
  width: 100%;
  z-index: 10;
  height: calc(100% - 6.5rem);
  .itemContent {
    margin: 0.5rem 0rem 0.7rem 0rem;
    padding: 0.5rem;
    border: 0.05rem solid $F5;
    border-radius: 0.5rem;
    .titlte {
      @include fr(flex-start);
      @include sc(0.9rem, $fColor);
      margin: 0.5rem;
      .headIco {
        @include wh(1rem, 1rem);
        border-radius: 5rem;
        margin-right: 0.5rem;
      }
      p{
        font-weight: bold;
      }
    }
    .middleContent{
      @include wh(auto,4rem);
      .line {
      @include fr(flex-start);
      margin: 0rem 0.5rem;
      p {
        @include sc(0.9rem, $fColor);
        margin-bottom: 0.3rem;
      }
      p:first-child {
        @include sc(0.9rem, $Tline);
        margin-right: 0.5rem;
      }
    }
    }
    .bottom {
      @include fr(space-between);
      padding: 0.8rem 0.5rem 0.5rem 0.5rem;
      border-top: 0.05rem solid $F5;
      p{
        @include sc(0.9rem,$fColor);
      }
      .left {
        @include fr(flex-start);
        .icoDiv {
          @include wh(2rem, 2rem);
          @include fc(center);
          border-radius: 5rem;
          margin-right: 0.5rem;
          i{
            margin-left: 0.15rem;
          }
        }
      }
    }
  }
}
</style>